import http from "../http-common";

class weatherDataService {
  csv(data) {
    return http.post("/csv/upload",data);
  }
  getAll() {
    return http.get("/weather");
  }
 
  get(id) {
    return http.get(`/weather/${id}`);
  }
  create(data) {
    return http.post("/weather", data);
  }
  findDate(data,data2){
    return http.get(`/date/${data},${data2}`)
  }
  update(id, data) {
    return http.put(`/weather/${id}`, data);
  }

  delete(id) {
    return http.delete(`/weather/${id}`);
  }

  deleteAll() {
    return http.delete(`/weather`);
  }

  // findByTitle(title) {
  //   return http.get(`/csv?title=${title}`);
  // }
}

export default new weatherDataService();